.y_calendar_header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.y_calendar_header_prev > i, .y_calendar_header_next > i {
  cursor: pointer;
}
.y_calendar_header_prev > i:first-child, .y_calendar_header_next > i:first-child {
  margin-right: 20px;
}
.y_calendar_header_label {
  cursor: pointer;
}
.y_calendar .el-date-table {
  width: 100%;
}