
.y_calendar {
  &_header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    &_prev,
    &_next {
      > i {
        cursor: pointer;
        &:first-child {
          margin-right: 20px;
        }
      }
    }
    &_label {
      cursor: pointer;
    }
  }
  .el-date-table {
    width: 100%;
  }
}
